*{
  padding: 0;
  margin: 0;
}

body{
  background-color: beige;
}

.adjust-nav-items{
  margin: 0 30px;
  color: #777b7f;
}

.adjust-nav-items:hover{
  text-decoration: none;
  color: #777b7f;
}

.active{
  border-bottom: 1px solid #777b7f; 
}

.nav-ele{
  display: flex;
  width: 10vw;
  justify-content: flex-start;
}

.nav-dark{
  width: 233px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  /* position: absolute;
  top: 18px;
  right: 28px; */
}

.navbar-align{
  justify-content: space-between;
}

.burger{
  display: none;
  position: absolute;
  top: 22px;
  right: 4%;
  cursor: pointer;
}

.line{
  width: 33px;
  background-color: gray;
  height: 5px;
  margin: 3px 3px;
}

.footer-container{
  height: 6vh;
  display: flex;
  justify-content: flex-end;
  align-items: center
}

@media only screen and (max-width: 1298px){

  .nav-ele-resp{
    margin-top: 10px;
    flex-direction: column;
  }
  .v-ele{
    display: none;
  }
  .navbar-resp{
    height:70px;
    flex-direction: column;
  }
  .navheight{
    height: 150px;
  }
  .burger{
    display: block;
  }
}

@media only screen and (max-width: 530px){
  .footer-container{
    flex-direction: column;
    justify-content: center;
    height: 15vh;
  }
}